import { FrontSide, BackSide } from 'react-flippy'
import styled, {css} from 'styled-components'
import python from '../../assets/python.svg'

export const ProfileContainer = styled.div` 
    display: flex;
    width: 100vw;
    height: 100vh;
    position: relative;
    justify-content: center;
    
    align-items:center;
    text-align:center;
    background-color:#E7E1D7;
    /* margin: 50px 0px; */
`

export const CardContainer = styled.div` 
    display: flex;
    width:30%;
    align-items:center;
    text-align:center;

    &:hover{
        cursor:pointer;
    }
`

export const FrontSideContainer = styled(FrontSide)`
    background-color: #E7E1D7;
    ${props=>props.reversed && 'background-color: #c6b79f'}
`

export const BackSideContainer = styled(BackSide)`
    background-color: #c6b79f;
    ${props=>props.reversed && 'background-color: #E7E1D7'}
`

export const HeadingContainer = styled.h1`

`

export const TitleContainer = styled.h3`

`

export const SubtitleContainer = styled.h5`
`

export const ParagraphContainer = styled.p`
    display:flex;
    align-items:center;
    justify-content:center;
    
`


export const ImageContainer = styled.div`

`

export const Logo = styled.img`
    height:30px;
    margin-right:10px;
`
export const StyledHR = styled.hr`
    width: 20%;
    margin-bottom:30px;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
`

export const Box = styled.div`
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 0.6rem/0.8rem;
    background-color: #c6b79f;
    overflow: hidden;
    box-shadow:
        inset 0 0 0.8rem 0.1rem rgba(darken(#c6b79f, 20%), 0.2),
        0 20px 30px -10px rgba(darken(#c6b79f, 20%), 0.26);
`