import React from 'react';
import Flippy from 'react-flippy'
import {ProfileContainer , CardContainer, FrontSideContainer, BackSideContainer, ParagraphContainer, TitleContainer, SubtitleContainer, ImageContainer, StyledHR, Logo} from './profile.styles'
import picture from '../../assets/picture.jpg'
import javascript from '../../assets/javascript.svg'
import python from '../../assets/python.svg'

const Profile = () => {
    return (
        <ProfileContainer>
            <CardContainer>
                <Flippy
                    flipOnClick={true} // default false
                    flipDirection="vertical" // horizontal or vertical
                    style={{ width: '650px', height: '750px' }} /// these are optional style, it is not necessary 
                >   
                    <FrontSideContainer reversed>
                        <TitleContainer>ABOUT</TitleContainer>
                        <StyledHR/>
                        <ImageContainer>
                            <img src={picture} width='50%' height='50%' alt=''/>
                        </ImageContainer>
                    </FrontSideContainer>
                    <BackSideContainer reversed> SKILLSET </BackSideContainer>
                    
                </Flippy>
            </CardContainer>

            <CardContainer>
                <Flippy
                    flipOnClick={true} // default false
                    flipDirection="horizontal" // horizontal or vertical
                    style={{ width: '650px', height: '750px' }} /// these are optional style, it is not necessary 
                >   
                    <FrontSideContainer> 
                        <TitleContainer>SKILLSET</TitleContainer>
                        <StyledHR/>
                        <SubtitleContainer>Confident at</SubtitleContainer>
                        <ParagraphContainer><Logo src={javascript}/><Logo src={python}/></ParagraphContainer> 
                        <SubtitleContainer>Confortable with</SubtitleContainer>
                        <ParagraphContainer>React, SQL, Java</ParagraphContainer>
                        <SubtitleContainer>I know this</SubtitleContainer>
                        <ParagraphContainer>C, C++, mongodb, express</ParagraphContainer>
                    </FrontSideContainer>
                    <BackSideContainer> SKILLSET </BackSideContainer>
                </Flippy>
            </CardContainer>

            
        </ProfileContainer>
        
    )
}
export default Profile
